.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a1f2e 0%, #0f1419 100%);
  padding: 20px;
}

.auth-box {
  background: #1e2433;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  max-width: 450px;
  width: 100%;
  padding: 40px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.auth-header {
  text-align: center;
  margin-bottom: 32px;
}

.auth-header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-bottom: 24px;
}

.logo-icon {
  font-size: 48px;
}

.auth-header h1 {
  font-size: 28px;
  color: #ffffff;
  margin: 0;
}

.auth-header h2 {
  font-size: 24px;
  color: #ffffff;
  margin: 0 0 8px 0;
}

.auth-header p {
  color: #a0aec0;
  font-size: 14px;
  margin: 0;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #e2e8f0;
  font-size: 14px;
  font-weight: 500;
}

.form-group input {
  background: #151921;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: #ffffff;
  font-size: 15px;
  padding: 12px 16px;
  transition: all 0.2s;
}

.form-group input:focus {
  outline: none;
  border-color: #4299e1;
  background: #1a1f2e;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.1);
}

.form-group input::placeholder {
  color: #718096;
}

.auth-button {
  background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
  border: none;
  border-radius: 8px;
  color: #ffffff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  padding: 14px 24px;
  transition: all 0.2s;
  margin-top: 8px;
}

.auth-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(66, 153, 225, 0.3);
}

.auth-button:active:not(:disabled) {
  transform: translateY(0);
}

.auth-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.auth-footer {
  margin-top: 24px;
  text-align: center;
  padding-top: 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.auth-footer p {
  color: #a0aec0;
  font-size: 14px;
  margin: 0;
}

.auth-footer a {
  color: #4299e1;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.auth-footer a:hover {
  color: #63b3ed;
}

.features-preview {
  margin-top: 24px;
  padding: 20px;
  background: rgba(66, 153, 225, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(66, 153, 225, 0.1);
}

.features-preview h3 {
  color: #4299e1;
  font-size: 16px;
  margin: 0 0 12px 0;
}

.features-preview ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.features-preview li {
  color: #cbd5e0;
  font-size: 14px;
}

/* Responsive */
@media (max-width: 500px) {
  .auth-box {
    padding: 24px;
  }

  .auth-header h1 {
    font-size: 24px;
  }

  .auth-header h2 {
    font-size: 20px;
  }

  .logo-icon {
    font-size: 36px;
  }
}
