/* Settings Component Styles */
.settings {
  padding: 0;
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  min-height: 100vh;
}

.settings-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 400px;
}

/* Layout */
.settings-layout {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 32px;
  align-items: start;
}

@media (max-width: 1024px) {
  .settings-layout {
    grid-template-columns: 1fr;
    gap: 24px;
  }
}

/* Sidebar Navigation */
.settings-sidebar {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 24px;
  position: sticky;
  top: 24px;
}

.settings-nav {
  display: flex;
  flex-direction: column;
  gap: 4px;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: thin;
  scrollbar-color: var(--brand-primary) transparent;
}

.settings-nav::-webkit-scrollbar {
  height: 4px;
  width: 4px;
}

.settings-nav::-webkit-scrollbar-track {
  background: transparent;
}

.settings-nav::-webkit-scrollbar-thumb {
  background: var(--brand-primary);
  border-radius: 2px;
}

.settings-nav::-webkit-scrollbar-thumb:hover {
  background: var(--accent-primary);
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  border: none;
  background: transparent;
  color: var(--text-secondary);
  border-radius: 8px;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  width: 100%;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
}

.nav-item.active {
  background: var(--gradient-primary);
  color: white;
  box-shadow: var(--shadow-md);
}

/* Settings Content */
.settings-content {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 32px;
  min-height: 600px;
}

/* Settings Section */
.settings-section {
  max-width: 800px;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 32px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--border-primary);
}

.section-header svg {
  color: var(--accent-primary);
}

.section-header h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 4px 0;
}

.section-header p {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

/* Setting Groups */
.setting-groups {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.setting-group {
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  padding: 24px;
  background: rgba(255, 255, 255, 0.02);
}

.setting-group h4 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 20px 0;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--border-primary);
}

/* Setting Items */
.setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 0;
  border-bottom: 1px solid var(--border-primary);
  gap: 24px;
}

.setting-item:last-child {
  border-bottom: none;
}

.setting-info {
  flex: 1;
}

.setting-info label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.setting-info span {
  font-size: 12px;
  color: var(--text-muted);
  line-height: 1.4;
}

.setting-control {
  flex-shrink: 0;
}

/* Switch Toggle */
.switch {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 24px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--border-secondary);
  transition: var(--transition-fast);
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: var(--transition-fast);
  border-radius: 50%;
  box-shadow: var(--shadow-sm);
}

input:checked + .slider {
  background: var(--gradient-primary);
}

input:checked + .slider:before {
  transform: translateX(24px);
}

input:disabled + .slider {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Input Controls */
.number-input,
.text-input {
  background: var(--secondary-bg);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  padding: 8px 12px;
  color: var(--text-primary);
  font-size: 14px;
  width: 120px;
  transition: all var(--transition-fast);
}

.text-input {
  width: 200px;
}

.number-input:focus,
.text-input:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.number-input:disabled,
.text-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.select-input {
  background: var(--secondary-bg);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  padding: 8px 12px;
  color: var(--text-primary);
  font-size: 14px;
  width: 140px;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.select-input:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.select-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.select-input option {
  background: var(--card-bg);
  color: var(--text-primary);
}

/* Save Actions Bar */
.settings-actions {
  position: fixed;
  bottom: 0;
  left: 280px;
  right: 0;
  background: var(--card-bg);
  border-top: 1px solid var(--border-primary);
  padding: 16px 24px;
  backdrop-filter: blur(10px);
  z-index: 100;
}

@media (max-width: 1024px) {
  .settings-actions {
    left: 0;
  }
}

.actions-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
}

.actions-info {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--accent-warning);
  font-size: 14px;
  font-weight: 500;
}

.actions-buttons {
  display: flex;
  gap: 12px;
}

/* Animations */
.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .settings-sidebar {
    position: static;
  }
  
  .settings-nav {
    flex-direction: row;
    overflow-x: auto;
    gap: 8px;
    padding-bottom: 8px;
  }
  
  .nav-item {
    white-space: nowrap;
    flex-shrink: 0;
    padding: 8px 12px;
    font-size: 13px;
  }
  
  .settings-content {
    padding: 24px 16px;
  }
  
  .setting-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .setting-control {
    width: 100%;
  }
  
  .number-input,
  .text-input,
  .select-input {
    width: 100%;
  }
  
  .actions-content {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }
  
  .actions-info {
    justify-content: center;
  }
  
  .actions-buttons {
    justify-content: center;
  }
}

@media (max-width: 640px) {
  .section-header {
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
  }
  
  .setting-groups {
    gap: 24px;
  }
  
  .setting-group {
    padding: 16px;
  }
  
  .setting-item {
    padding: 12px 0;
  }
}

/* Dark mode optimizations */
@media (prefers-color-scheme: dark) {
  .switch input:checked + .slider {
    background: var(--gradient-primary);
  }
  
  .slider:before {
    background-color: #ffffff;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .setting-item {
    border-bottom-color: var(--text-primary);
  }
  
  .setting-group {
    border-color: var(--text-primary);
  }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .slider,
  .slider:before,
  .nav-item,
  .number-input,
  .text-input,
  .select-input {
    transition: none;
  }
  
  .spinning {
    animation: none;
  }
}

/* Button Styles for Demo */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  background: var(--accent-primary);
  color: white;
  min-height: 40px;
}

.btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.3);
}

.btn.btn-secondary {
  background: var(--card-bg);
  color: var(--text-primary);
  border: 1px solid var(--border-primary);
}

/* Storage Monitor Styles */
.storage-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-bottom: 20px;
}

.storage-stat-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 12px;
  background: var(--bg-secondary);
  border-radius: 8px;
}

.storage-stat-item .stat-label {
  font-size: 12px;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.storage-stat-item .stat-value {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.storage-stat-item .stat-value.success {
  color: var(--success-color);
}

.storage-bar {
  position: relative;
  height: 32px;
  background: var(--bg-secondary);
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 20px;
}

.storage-bar-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--accent-primary), var(--accent-secondary));
  transition: width 0.3s ease;
}

.storage-percentage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 13px;
  font-weight: 600;
  color: var(--text-primary);
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
}

.storage-breakdown {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.breakdown-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px;
  background: var(--bg-secondary);
  border-radius: 8px;
  font-size: 14px;
  color: var(--text-primary);
}

.breakdown-item svg {
  color: var(--accent-primary);
}

.usage-badge {
  margin-left: auto;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.usage-badge.success {
  background: var(--success-bg);
  color: var(--success-color);
}

.usage-badge.warning {
  background: var(--warning-bg);
  color: var(--warning-color);
}

.storage-warning {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: var(--warning-bg);
  border: 1px solid var(--warning-color);
  border-radius: 8px;
  color: var(--warning-color);
  font-size: 13px;
  margin-top: 12px;
}

.storage-warning svg {
  flex-shrink: 0;
}

.storage-loading {
  padding: 20px;
  text-align: center;
  color: var(--text-secondary);
  font-size: 14px;
}

/* Theme Toggle Styles */
.theme-toggle {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 24px;
  background: var(--brand-gradient);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.theme-toggle:hover {
  opacity: 0.9;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.theme-preview {
  margin-top: 24px;
  padding: 20px;
  background: var(--bg-tertiary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.theme-preview h5 {
  margin: 0 0 16px 0;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 600;
}

.preview-colors {
  display: flex;
  gap: 12px;
}

.color-swatch {
  flex: 1;
  height: 80px;
  border-radius: 8px;
  display: flex;
  align-items: flex-end;
  padding: 12px;
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
}

.color-swatch span {
  color: white;
  font-size: 12px;
  font-weight: 500;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Scheduler Settings Styles */
.schedule-info {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 16px;
  padding: 16px;
  background: var(--info-bg);
  border: 1px solid var(--info-border);
  border-radius: 8px;
  color: var(--info-text);
}

.schedule-info strong {
  color: var(--text-primary);
  font-weight: 600;
}

.setting-input {
  padding: 8px 12px;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  color: var(--text-primary);
  font-size: 14px;
  transition: all 0.2s ease;
}

.setting-input:focus {
  outline: none;
  border-color: var(--brand-primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.setting-select {
  padding: 8px 12px;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.setting-select:focus {
  outline: none;
  border-color: var(--brand-primary);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.btn.btn-secondary:hover {
  background: var(--border-primary);
  border-color: var(--accent-primary);
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.2);
}