/* Sidebar Component Styles */
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 280px;
  background: var(--sidebar-bg);
  border-right: 1px solid var(--border-primary);
  display: flex;
  flex-direction: column;
  z-index: 1075;
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease-in-out;
}

/* Header Section */
.sidebar-header {
  padding: 24px;
  border-bottom: 1px solid var(--border-primary);
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
}

.logo-icon {
  width: 48px;
  height: 48px;
  background: var(--gradient-primary);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: var(--shadow-neon);
}

.logo-text {
  flex: 1;
}

.brand-name {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0;
  line-height: 1.2;
}

.brand-tagline {
  font-size: 12px;
  color: var(--text-muted);
  margin: 0;
  margin-top: 2px;
}

/* Connection Status */
.connection-status {
  padding: 16px 24px;
  border-bottom: 1px solid var(--border-primary);
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 12px;
}

.status-indicator.connected {
  background: rgba(16, 185, 129, 0.1);
  color: var(--accent-success);
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.status-indicator.disconnected {
  background: rgba(239, 68, 68, 0.1);
  color: var(--accent-danger);
  border: 1px solid rgba(239, 68, 68, 0.2);
}

.system-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.system-stat {
  display: flex;
  flex-direction: column;
  padding: 8px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 6px;
}

.stat-label {
  font-size: 11px;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-value {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin-top: 2px;
}

.threat-count {
  color: var(--accent-danger);
}

/* Navigation */
.sidebar-nav {
  flex: 1;
  padding: 16px 0;
  overflow-y: auto;
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin: 0 16px 4px 16px;
  position: relative;
}

.nav-link {
  display: block;
  text-decoration: none;
  color: var(--text-secondary);
  border-radius: 12px;
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
}

.nav-link.active {
  background: var(--gradient-primary);
  color: white;
  box-shadow: var(--shadow-md);
}

.nav-content {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  position: relative;
}

.nav-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
}

.nav-label {
  flex: 1;
  font-size: 14px;
  font-weight: 500;
}

.nav-badge {
  background: var(--accent-danger);
  color: white;
  font-size: 11px;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
  margin-right: 8px;
}

.nav-arrow {
  opacity: 0;
  transition: opacity var(--transition-fast);
}

.nav-link:hover .nav-arrow {
  opacity: 1;
}

.nav-link.active .nav-arrow {
  opacity: 1;
}

.active-indicator {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: white;
  border-radius: 0 2px 2px 0;
}

/* Protection Status */
.protection-status {
  padding: 16px 24px;
  border-top: 1px solid var(--border-primary);
}

.protection-card {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.protection-header {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.protection-icon {
  width: 40px;
  height: 40px;
  background: var(--gradient-primary);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.protection-info h3 {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 2px 0;
}

.protection-state {
  font-size: 12px;
  font-weight: 500;
  margin: 0;
}

.protection-state.active {
  color: var(--accent-success);
}

.protection-state.inactive {
  color: var(--accent-danger);
}

.protection-toggle {
  width: 44px;
  height: 24px;
  border-radius: 12px;
  position: relative;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.protection-toggle.on {
  background: var(--accent-success);
}

.protection-toggle.off {
  background: var(--border-secondary);
}

.toggle-indicator {
  width: 20px;
  height: 20px;
  background: white;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  transition: all var(--transition-fast);
  box-shadow: var(--shadow-sm);
}

.protection-toggle.on .toggle-indicator {
  transform: translateX(20px);
}

.protection-toggle.off .toggle-indicator {
  transform: translateX(2px);
}

/* Footer */
.sidebar-footer {
  padding: 16px 24px;
  border-top: 1px solid var(--border-primary);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.power-button {
  width: 40px;
  height: 40px;
  background: var(--gradient-danger);
  border: none;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-fast);
}

.power-button:hover {
  transform: scale(1.1);
  box-shadow: var(--shadow-lg);
}

.version-info {
  text-align: right;
}

.version-info p {
  font-size: 11px;
  color: var(--text-muted);
  margin: 0;
  line-height: 1.4;
}

/* Mobile Responsive */
@media (max-width: 1024px) {
  .sidebar {
    transform: translateX(-100%) !important;
    transition: transform 0.3s ease-in-out;
    box-shadow: var(--shadow-lg);
  }
  
  .sidebar.open {
    transform: translateX(0) !important;
  }
}

@media (max-width: 768px) {
  .sidebar {
    width: 280px;
    max-width: 85vw;
  }
  
  .sidebar-header {
    padding: 20px;
  }
  
  .logo-icon {
    width: 42px;
    height: 42px;
  }
  
  .brand-name {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .sidebar {
    width: 100%;
    max-width: 100vw;
  }
}

/* User Profile Section */
.user-profile {
  margin-top: auto;
  padding: 16px 20px;
  border-top: 1px solid var(--border-primary);
  background: rgba(255, 255, 255, 0.02);
}

.profile-card {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.profile-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--accent-primary) 0%, var(--accent-info) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  flex-shrink: 0;
}

.profile-info {
  flex: 1;
  min-width: 0;
}

.profile-info h4 {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 4px 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tier-badge {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  font-size: 11px;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tier-badge.free {
  background: rgba(160, 174, 192, 0.1);
  color: var(--text-secondary);
}

.tier-badge.premium {
  background: linear-gradient(135deg, #f6ad55 0%, #ed8936 100%);
  color: white;
}

.profile-actions {
  display: flex;
  gap: 8px;
}

.upgrade-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  background: linear-gradient(135deg, #f6ad55 0%, #ed8936 100%);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 8px 12px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.upgrade-button:hover {
  box-shadow: 0 4px 12px rgba(246, 173, 85, 0.3);
}

.logout-button {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
}

.logout-button:hover {
  background: rgba(255, 77, 79, 0.1);
  border-color: var(--accent-danger);
  color: var(--accent-danger);
}

/* Scrollbar for nav */
.sidebar-nav::-webkit-scrollbar {
  width: 4px;
}

.sidebar-nav::-webkit-scrollbar-track {
  background: transparent;
}

.sidebar-nav::-webkit-scrollbar-thumb {
  background: var(--border-primary);
  border-radius: 2px;
}

/* Shutdown Dialog */
.shutdown-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: 20px;
}

.shutdown-dialog {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 32px;
  max-width: 450px;
  width: 100%;
  box-shadow: var(--shadow-xl);
  border: 1px solid var(--border-primary);
}

.dialog-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
}

.dialog-icon {
  color: var(--accent-danger);
  flex-shrink: 0;
}

.dialog-header h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.dialog-content {
  margin-bottom: 24px;
}

.dialog-content p {
  color: var(--text-secondary);
  margin: 0 0 16px 0;
  line-height: 1.6;
}

.dialog-content ul {
  list-style: none;
  padding: 0;
  margin: 16px 0;
}

.dialog-content li {
  padding: 8px 0;
  padding-left: 24px;
  position: relative;
  color: var(--text-secondary);
}

.dialog-content li::before {
  content: '';
  position: absolute;
  left: 8px;
  color: var(--accent-danger);
  font-weight: bold;
}

.dialog-content .warning {
  color: var(--accent-warning);
  font-weight: 500;
  margin-top: 16px;
}

.dialog-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.dialog-button {
  padding: 12px 24px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-family: inherit;
}

.dialog-button.cancel {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.dialog-button.cancel:hover {
  background: var(--border-secondary);
}

.dialog-button.shutdown {
  background: var(--accent-danger);
  color: white;
}

.dialog-button.shutdown:hover {
  background: #dc2626;
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
}

@media (max-width: 480px) {
  .shutdown-dialog {
    padding: 24px;
  }
  
  .dialog-header h3 {
    font-size: 20px;
  }
  
  .dialog-actions {
    flex-direction: column-reverse;
  }
  
  .dialog-button {
    width: 100%;
  }
}

.sidebar-nav::-webkit-scrollbar-thumb:hover {
  background: var(--border-secondary);
}