/* Scanner Component Styles */
.scanner {
  padding: 0;
  max-width: 1400px;
  margin: 0 auto;
}

.scanner-layout {
  display: grid;
  grid-template-columns: 380px 1fr;
  gap: 32px;
  align-items: start;
}

@media (max-width: 1200px) {
  .scanner-layout {
    grid-template-columns: 320px 1fr;
    gap: 24px;
  }
}

@media (max-width: 1024px) {
  .scanner-layout {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .scan-controls {
    position: static;
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .scanner-layout {
    gap: 20px;
  }
  
  .scan-controls {
    padding: 20px;
  }
  
  .type-options {
    grid-template-columns: 1fr 1fr;
    gap: 12px;
  }
  
  .type-option {
    padding: 14px 10px;
    font-size: 13px;
  }
}

@media (max-width: 640px) {
  .scanner {
    padding: 0;
  }
  
  .scanner-layout {
    gap: 16px;
  }
  
  .scan-controls {
    padding: 16px;
  }
  
  .control-header h3 {
    font-size: 16px;
  }
  
  .type-options {
    gap: 10px;
  }
  
  .type-option {
    padding: 12px 8px;
    font-size: 12px;
  }
  
  .path-input-section label {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .scan-controls {
    padding: 14px;
  }
  
  .type-options {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  
  .type-option {
    flex-direction: row;
    justify-content: center;
    padding: 14px 12px;
  }
}

/* Scan Controls */
.scan-controls {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 24px;
  position: sticky;
  top: 24px;
}

.control-header {
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-primary);
}

.control-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

/* Scan Type Selector */
.scan-type-selector {
  margin-bottom: 24px;
}

.type-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.type-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 16px 12px;
  background: var(--secondary-bg);
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: 14px;
  font-weight: 500;
}

.type-option:hover {
  border-color: var(--accent-primary);
  background: var(--tertiary-bg);
  color: var(--text-primary);
}

.type-option.active {
  background: var(--gradient-primary);
  border-color: transparent;
  color: white;
  box-shadow: var(--shadow-md);
}

/* Path Input */
.path-input-section {
  margin-bottom: 24px;
}

.path-input-section label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.path-input-group {
  display: flex;
  gap: 8px;
}

.path-input {
  flex: 1;
}

.browse-btn {
  flex-shrink: 0;
  padding: 12px 16px;
}

/* Quick Scan Options */
.quick-scan-section {
  margin-bottom: 24px;
}

.quick-scan-section h4 {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 16px 0;
}

.quick-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.quick-option {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: var(--secondary-bg);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  text-align: left;
  cursor: pointer;
  transition: all var(--transition-fast);
  color: var(--text-secondary);
}

.quick-option:hover {
  border-color: var(--accent-primary);
  background: var(--tertiary-bg);
  color: var(--text-primary);
}

.quick-option:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.option-content {
  flex: 1;
}

.option-content h5 {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin: 0 0 2px 0;
}

.option-content p {
  font-size: 12px;
  color: var(--text-muted);
  margin: 0;
}

/* Scan Actions */
.scan-actions {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid var(--border-primary);
}

.scan-btn {
  width: 100%;
  padding: 16px;
  font-size: 16px;
  font-weight: 600;
}

/* Scan Content */
.scan-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* Progress Section */
.scan-progress-section {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 24px;
}

.progress-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.progress-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.progress-percentage {
  font-size: 24px;
  font-weight: 700;
  color: var(--accent-primary);
}

.current-file {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 16px;
  padding: 12px;
  background: rgba(79, 70, 229, 0.1);
  border: 1px solid rgba(79, 70, 229, 0.2);
  border-radius: 8px;
  color: var(--accent-primary);
  font-size: 14px;
}

.scan-stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-top: 20px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.stat-value.threat {
  color: var(--accent-danger);
}

.stat-label {
  font-size: 12px;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Results Section */
.scan-results-section {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 24px;
}

.results-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-primary);
}

.results-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.results-count {
  font-size: 12px;
  color: var(--text-muted);
  background: rgba(255, 255, 255, 0.05);
  padding: 4px 8px;
  border-radius: 6px;
}

.results-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.result-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  border: 1px solid var(--border-primary);
  border-radius: 12px;
  transition: all var(--transition-fast);
  background: rgba(255, 255, 255, 0.02);
}

.result-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateX(4px);
}

.result-item.success {
  border-left: 4px solid var(--accent-success);
}

.result-item.danger {
  border-left: 4px solid var(--accent-danger);
}

.result-item.warning {
  border-left: 4px solid var(--accent-warning);
}

.result-icon {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.result-icon.success {
  background: var(--gradient-success);
}

.result-icon.danger {
  background: var(--gradient-danger);
}

.result-icon.warning {
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
}

.result-content {
  flex: 1;
}

.result-content h4 {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 8px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.result-meta {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.threat-type {
  padding: 2px 8px;
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.threat-type.success {
  background: rgba(16, 185, 129, 0.1);
  color: var(--accent-success);
  border: 1px solid var(--accent-success);
}

.threat-type.danger {
  background: rgba(239, 68, 68, 0.1);
  color: var(--accent-danger);
  border: 1px solid var(--accent-danger);
}

.threat-type.warning {
  background: rgba(245, 158, 11, 0.1);
  color: var(--accent-warning);
  border: 1px solid var(--accent-warning);
}

.threat-name {
  font-size: 12px;
  color: var(--text-secondary);
  font-weight: 500;
}

.file-size {
  font-size: 12px;
  color: var(--text-muted);
}

.vt-badge {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 6px;
  font-size: 11px;
  font-weight: 600;
  transition: all var(--transition-fast);
  cursor: help;
}

.vt-badge:hover {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  transform: scale(1.05);
}

.results-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.btn.btn-info {
  background: rgba(59, 130, 246, 0.1);
  color: #3b82f6;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.btn.btn-info:hover {
  background: rgba(59, 130, 246, 0.2);
  border-color: rgba(59, 130, 246, 0.5);
}

.result-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.btn-sm {
  padding: 8px 12px;
  font-size: 12px;
}

/* Scan History */
.scan-history-section {
  background: var(--card-bg);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  padding: 24px;
}

.history-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-primary);
}

.history-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.history-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  transition: all var(--transition-fast);
  cursor: pointer;
}

.history-item:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--accent-primary);
}

.history-icon {
  width: 32px;
  height: 32px;
  background: var(--gradient-primary);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.history-content {
  flex: 1;
}

.history-content h5 {
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin: 0 0 4px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.history-meta {
  display: flex;
  align-items: center;
  gap: 12px;
}

.scan-time {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 11px;
  color: var(--text-muted);
}

.scan-duration {
  font-size: 11px;
  color: var(--text-muted);
  background: rgba(255, 255, 255, 0.05);
  padding: 2px 6px;
  border-radius: 4px;
}

.history-stats {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2px;
}

.files-count {
  font-size: 12px;
  color: var(--text-secondary);
}

.threats-count {
  font-size: 11px;
  color: var(--accent-danger);
  background: rgba(239, 68, 68, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  border: 1px solid var(--accent-danger);
}

/* Responsive Design */
@media (max-width: 768px) {
  .scan-controls {
    position: static;
  }
  
  .type-options {
    grid-template-columns: 1fr;
  }
  
  .path-input-group {
    flex-direction: column;
  }
  
  .scan-stats-grid {
    grid-template-columns: 1fr;
  }
  
  .result-item {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .result-actions {
    width: 100%;
    justify-content: stretch;
  }
  
  .result-actions .btn {
    flex: 1;
    min-width: 80px;
  }
  
  .history-item {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .history-stats {
    align-items: flex-start;
    width: 100%;
  }
}